 background: -webkit-gradient(linear, left top, right top, from(rgb(88, 218, 179)), to(rgb(151, 81, 174)));
 background: -webkit-linear-gradient(left, rgb(88, 218, 179), rgb(151, 81, 174));
 background: -o-linear-gradient(left, rgb(88, 218, 179), rgb(151, 81, 174));
 background: linear-gradient(90deg, rgb(88, 218, 179), rgb(151, 81, 174));
  
}


